import React from "react";
import TodoFooter from './TodoFooter';

const styles = {
    "height": "100%",
    "width": "100%",
    "display": "flex",
    "flexDirection":"column",
    "padding":"27px"
}

const titleStyle = {
    "width":"40%",
    "border":"0",
    "borderBottom":"2px solid cornflowerblue",
    "marginBottom":"50px",
    "textAlign":"center"
}

const descStyle = {
    "width":"90%",
    "min-height":"55%",
    "max-height":"55%",
    "padding":"15px",
    "wordWrap":"break-word"
}

export default function Todo({title,desc}){
    var today = new Date();
    today = String(today.getDate()).padStart(2,'0') + '/' + String(today.getMonth()+1).padStart(2,'0') + '/' + String(today.getFullYear());

    return(
        <div style={styles}>
            <h6 style={{"fontSize":"15px","marginBottom":"15px"}}>{today}</h6>
            <input type="text" style={titleStyle} placeholder={title ? title : "Title Here"}/>
            <textarea style={descStyle} placeholder={!desc ? "Here goes the description of Todo... " : null} value={desc ? desc : null}/>
            <TodoFooter/>
        </div>
        
    )
}