// Props work only in Class Components

import React from "react";
import PropTypes from 'prop-types';

const styles = {
    "width":"98vw",
    "height":"30px",
    "backgroundColor":"black",
    "color":"white",
    "textAlign":"center",

}

class Footer extends React.Component{
    render(){
        return(
            <p style={styles}>&copy; by {this.props.name}</p>
        );
    }
} 

Footer.defaultProps = {
    name : "default"
}

Footer.propTypes = {
    name : PropTypes.string.isRequired
}

export default Footer;